@import '../../global/utils/init';

.page {
	.l-header {
		.block--inner-wrapper {
			max-width: 1140px;
			margin: 0 auto;
		}
	}
	.l-container {
		max-width: 1140px;
		margin: 0 auto;
	}
	img {
		width: 100%;
	}
}

.block-views-blog-block {
	background-color: rgba(216,228,213,.52);
	.view-blog {
		padding: 50px 0;
		.view-header {
			text-align: center;
			padding-bottom: 20px;
		}
		.view-content {
		  grid-row-gap: 50px;
		  grid-template-columns: auto auto auto;
		  padding: 10px;

		}
	}
}

.block-views-blog-block-1 {
	.block--inner-wrapper {
		.view-content {
			.views-row{
				margin-bottom: 0px;
				margin-bottom: 15px;
				.field-content {
					display: grid;
					grid-template-columns:50% 50%;
				}
				img {
					width: 100%;
					height: 100%;
					max-height: 400px;
					min-height: 400px;
				}
				&.odd {
					background: #f5d23b;
					.field-content {
						.card-list-left {
							order:1;
							img {
								width: 100%;
							}
						}
						.card-list-right {
							display: grid;
							place-items: center;
						    text-align: left;
							padding: 0 15px;
							.card-list-details {
								max-width: 400px;
								a {
									font-size: 22px;
	    							font-weight: 500;	
									text-decoration: none;
								}
								.role {
									font-size: 14px;
									padding-top: 20px;
								}
							}
						}
					}
				}
				&.even {
					background: #cf5840;
					.field-content {
						.card-list-right {
							display: grid;
							place-items: center;
						    text-align: left;
							padding: 0 15px;
							.card-list-details {
								max-width: 400px;
								a {
									font-size: 22px;
	    							font-weight: 500;	
									text-decoration: none;
								}
								.role {
									font-size: 14px;
									padding-top: 20px;
								}
							}
						}
					}
				}
			} 
		}
	}
}
